// @flow

import mysql from 'mysql';
import fs from 'fs';
import type {SQLCredentials} from '../types/types';

const credentials : SQLCredentials = JSON.parse(fs.readFileSync('../config.json', 'utf8'));
initDatabase(credentials);
console.log();

export default function initDatabase(credentials: SQLCredentials) {
    var con = mysql.createConnection({
        host: credentials.host,
        user: credentials.user,
        password: credentials.password
    });

    return con;
}
